import { lazy, Suspense, useEffect, useRef } from "react";
import { Link, useParams, useRouteMatch } from "react-router-dom";
import Loading from '../App/Loading';
import M from "materialize-css";

export default function PracticeHindi() {

    const

    { page } = useParams<{ page: string }>(),
    dropdownRef = useRef<any>(null),
    match = useRouteMatch(),
    LazyImport = lazy(
      () => import("./PracticeHindi/P" + (page || 0))
    ),
    contents = [
      "Hello Hindi",
      "Shopping",
      "Numbers",
      "Time",
      "Dates"
    ],

    setProgress = (progress: number) => {
      dropdownRef.current.style.background = "linear-gradient(to right, #26a69a " + progress * 100 + "%, #808080 " + progress * 100 + "%)";
    };

    useEffect(() => {
      const instance = M.Dropdown.init(dropdownRef.current);
      return () => instance.destroy();
    });

    useEffect(() => {
      page && setProgress(0);
    }, [page]);

    return (
      <>
        <Link
        className="dropdown-trigger btn"
        data-target="dropdown"
        ref={dropdownRef}
        to="!#">{
          page ? contents[+page - 1] : "Drop Me!"
        }</Link>
        <ul className="dropdown-content" id="dropdown">
          {contents.map((value, key) => (
            <li key={key}>
              <Link to={match.path.replace('/page/:page', '') + '/page/' + (key + 1)}>
                {"Lesson " + (key + 1) + ": " + value}
              </Link>
            </li>
          ))}
        </ul>
        <Suspense fallback={Loading()}>
          <LazyImport setProgress={setProgress} />
        </Suspense>
      </>
    );
};